.mat-card {
  border-radius: 10px;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.1);
  height: 100%;
}

.fuse-label {
  color: #000;
  font-size: 13px;
  font-family: "GothamNarrowBold", sans-serif;
}

.fuse-text {
  font-family: "gothamlight", sans-serif;
  font-weight: bold;
  color: #000;
  font-size: 13px;
  opacity: 0.5;
}

.example-container {
  width: 100%;
