}

.addfuseDiff {
  height: 34px !important;
  padding: 0px 30px !important;
  border-radius: 6px !important;
  line-height: 34px;
  position: absolute;
  right: 20px;
  top: 15px;
}

.cardheader {
  background: #fff;
}

.mat-drawer-content .mat-card {
  height: calc(100vh - 290px);
}

.fuse-container {
  height: 100%;
}

