/* Responsive css start*/
@media screen and (max-width: 767px) {
  table,
  thead,
  tbody,
  th,
  td,
  tr {
    display: inline-block;
  }

  .textup {
    padding-top: 2px;
  }

  .mat-form-field {
    width: 100%;
  }

  .fuse-block {
    margin-top: 8px;
  }

  ::ng-deep .mat-drawer-inner-container {
    height: 97% !important;
    overflow: hidden !important;
  }

  table {
    width: 100%;
  }

  .mat-card {
    margin-bottom: 45px;
  }

  .addfuseDiff {
    right: 35%;
    bottom: 15px;
    top: initial;
  }

  .editadd {
    right: 2% !important;
    bottom: 15px;
    top: initial !important;
  }

  p.panel-header-text {
    width: 100% !important;
    margin-top: 5px;
  }

  .addcon {
    top: 10px !important;
  }

  .norecord {
    background: #fff;
  }

  .norecord tr {
    border: none !important;
    margin-left: 25%;
  }

  .norecord tr td {
    border-bottom: none !important;
    padding-left: 0;
    text-align: center !important;
  }

  thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  tr {
    border: 1px solid #ccc;
  }

  td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
  }

  td span.textTtl {
    display: inline-block !important;
    position: absolute;
    top: 50%;
    /* left: 25px; */
    left: 7px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    transform: translate(0, -50%);
    font-weight: bold;
    color: #000;
  }

  table tr {
    height: 100%;
    margin-bottom: 25px;
  }

  table tr {
    border: none;
    margin-bottom: 20px;
    border-radius: 10px;
    width: 100%;
    -webkit-box-shadow: 0px 0px 16px -1px rgba(217, 217, 217, 1);
    -moz-box-shadow: 0px 0px 16px -1px rgba(217, 217, 217, 1);
    box-shadow: 0px 0px 16px -1px rgba(217, 217, 217, 1);
  }

  .example-sidenav-content {
    background: #ececef;
  }

  .mat-table {
    background: transparent;
  }

  tr.mat-header-row {
    height: 0 !important;
  }

  table tr th {
    display: none;
  }

  ::ng-deep .fuse-outer.fuse-part table thead {
    display: none;
  }

  .btn-toggle:after {
    width: 5rem;
  }

  table tr td {
    width: 100% !important;
    padding-left: 50% !important;
    text-align: left !important;
    height: 45px;
    word-break: break-all;
    line-height: 12px;
    padding-top: 15px;
    padding-right: 15px;
    border: none;
  }

  table tr td:last-child {
    padding-top: 5px;
  }

  .example-container tr td:last-child {
    max-width: 100%;
  }

  .pad {
    padding-bottom: 0px;
    flex-direction: row !important;
  }

  .pad .heading {
    width: 35%;
    text-align: left !important;
  }

  .pad span {
    width: 65%;
  }

  .fusepart {
    flex-wrap: wrap;
  }

  .fusepart .fuse-block {
    flex-direction: row !important;
    justify-content: space-between;
  }

  .cancel-btn {
    min-width: 0;
    padding: 10px 20px;
    line-height: 14px !important;
    margin-bottom: 15px;
  }

  .modal-footer {
    margin-top: 0 !important;
  }

  .save-change {
    min-width: 0;
    padding: 10px 3px;
    line-height: 14px !important;
    margin-bottom: 15px;
  }

  #status-toggle .btn-toggle {
    margin: 0 4.5rem;
  }

  .btn-toggle:before {
    left: -4.5rem;
  }

  .addfuse {
    padding: 10px 16px !important;
    height: auto !important;
    line-height: 14px !important;
  }

  .updateBtn .save-change {
    padding: 6px 12px !important;
  }

  .fuselist {
    padding: 20px 0 0 0 !important;
  }

  .portal-outer {
    padding: 0 20px;
  }

  .title-container span {
    width: 100%;
    display: block;
    border-radius: 0;
    border-top: 2px solid #ccc;
    border-bottom: 2px solid #ccc;
    font-size: 17px;
  }

  .heading.Hbtn {
    margin-left: 0 !important;
  }

  .title-container {
    margin: 0 0 20px;
  }

  .fuse-outer {
    overflow: auto !important;
    height: calc(100vh - 350px);
    padding: 10px 4px;
  }

  ::ng-deep .modal-dialog {
    padding: 0 10px;
  }

  .logo_container,
  .edit-logo {
    width: 100% !important;
    height: 200px !important;
  }

  .log-head h5 {
    margin-top: 0 !important;
  }

  .frlog img {
    margin-top: 0 !important;
    width: 100%;
  }

  .contpart {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 12px 5px;
  }

  .contpart div:nth-child(2) {
    text-align: center !important;
  }

  .errorcon {
    width: 100% !important;
  }

  .errorcard {
    margin-bottom: 0;
  }

  .errorcard > div {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .filepart {
    height: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .dumimg {
    width: 100% !important;
    height: auto !important;
  }

  .fuselist .ques-icon .qicon {
    bottom: 18px;
  }
}

@media screen and (max-width: 374px) {
  table tr td {
    padding-left: 43% !important;
    font-size: 12px;
  }

  table tr td:last-child {
    padding-top: 10px;
  }
}

@media screen and (min-width: 320px) and (max-width: 440px) {
